
// TO add
// font-family
// width

$rule-height: 40px; // <-- primary parameter
// depending on your chosen font-family, you might want to play around with these. 
// For example, 'Marck Script' is much larger than 'Reenie Beanie'
$min-font-size: 10pt; // 8pt for 'Marck Script'
$max-font-size: 20pt; // 13pt for 'Marck Script'
$rule-height-to-font-size-factor: 0.7; // .5 for 'Marck Script'

// auto-sizing calculations
$font-size: min(max($rule-height * $rule-height-to-font-size-factor, $min-font-size), $max-font-size);
$rule-mask-height: $rule-height - 1;
$rule-padding-top: $rule-height + 2;
$rule-padding-right: $rule-height;
$rule-padding-left: $rule-height * 1.5;

// colors
$hole-fill-color: #f5f5f5;
$hole-shadow: #CCCCCC;
$paper-color: #FFFFFF;
$line-color: #E7EFF8;
$text-color: #000000;

.notepad {
    color: $text-color;
    border: 1px solid #EEEEEE;
    box-shadow: 1px 1px 0 #DDDDDD;
    display: inline-block;
    vertical-align: top;
    font-size: $font-size;
    line-height: $rule-height;
    margin: 2% auto;
    padding: $rule-padding-top $rule-height 3px $rule-padding-left;
    resize: none;   
    min-height: 150px;
    background-color: $paper-color;
    background-image: 
      -moz-linear-gradient(top, transparent, transparent $rule-mask-height, $line-color 0px), 
      -moz-radial-gradient(4% 50%, circle closest-corner, $hole-fill-color, $hole-fill-color 39%, transparent 0%), 
      -moz-radial-gradient(3.9% 46%, circle closest-corner, $hole-shadow, $hole-shadow 43.5%, transparent 0%);
    background-image: 
      -webkit-linear-gradient(top, transparent, transparent $rule-mask-height, $line-color 0), 
      -webkit-radial-gradient(4% 50%, circle closest-corner, $hole-fill-color, $hole-fill-color 39%, transparent 0%), 
      -webkit-radial-gradient(3.9% 46%, circle closest-corner, $hole-shadow, $hole-shadow 43.5%, transparent 0%);
    -webkit-background-size: 100% $rule-height;
    background-size: 100% $rule-height;
}