.hero {
	color: #ffffff;
	text-align: center;
	height: 100vh;
	min-height: 550px;
	background-color: #333;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;

	&:after {
		content: "";
		background: linear-gradient(to bottom,rgba(0,0,0,.75) 0,rgba(0,0,0, 0) 100%);
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		height: 200px;
	}

	p {
		color: #fff;
	}

	.inner-hero {
		display: flex;
		align-items: center;
		height: 100%;
		position: relative;
	}

	.hero-text-container {
		max-width: 600px;
		text-align: left;
		font-family: $primary-font;

		h1, h2, p, .button {
			text-shadow: 1px 1px 1px rgba(0,0,0,.8);
			margin-bottom: 40px;
			
			em {
				font-size: 42px;
				display: block;
				text-transform: uppercase;
				font-family: $primary-font;
			}
		}

		h1, h2 {
			font-family: $manuscrit-font;
		}

		fieldset {
			border: 1px solid #fff;
			border-radius: 10px;
			padding: 10px 20px 10px 10px;

			legend {
				margin-left: 15px;
				text-transform: uppercase;
				text-shadow: 1px 1px 1px rgba(0,0,0,.8);
			}
		}
	}

	.read-more {
		display: block;
		color: white;
		font-size: 1.3em;
		text-decoration: none;
		font-family: $secondary-font;
		font-weight: 900;
		text-transform: uppercase;
		position: relative;
		padding-left: 120px;
		letter-spacing: 2px;
		text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.8);
		margin-left: 100px;
	
		&:after {
			left: 0;
			display: block;
			content: " ";
			height: 1px;
			width: 100px;
			background-color: white;
			position: absolute;
			top: 50%;
			right: 200px;
		}

		&:hover {
			letter-spacing: 10px;
		}
		&:hover::after {
			height: 2px;
			width: 110px;
		}
	}


	form {
		border: #FFF solid 1px;
		border-radius: 10px;
		background-color: transparent;
		padding: 20px 30px;
		margin: 0;
		max-width: 400px;
		font-family: $secondary-font;

		label, .indicates-required {
			text-transform: uppercase;
			letter-spacing: 1.5px;
		}
		.indicates-required {
			display: none;
		}

		input[type=submit] {
			background-color: rgba($color: $secondary-color, $alpha: .9);
			color: #000;
			border-radius: 5px;
			border: #FFF solid 1px;
			
			&:hover {
				background-color: rgba($color: $primary-color, $alpha: .8);
				color: #FFF;
			}
		}

		input[type=text], input[type=email] {
			font-family: $primary-font;
			background-color: rgba(#FFF, 0.7);
			border: #ccc solid 1px;
		}

		div.response {
			text-align: center;
			font-family: $primary-font;
			font-size: 1.2em;
			letter-spacing: 1.5px;
			width: 100%;
			padding: 5px 0;
			margin: 5px 0;
			margin-bottom: 10px;
			background-color: rgba($color: #000, $alpha: .6);
			border: none;
			border-radius: 5px;
		}
		
		#mce-success-response {
			color: #82F264 !important;
		}
		#mce-error-response {
			color: #ff8d8d !important;
		}
 	} 
}

.half-hero {
	height: 50vh;
}

.photo-credits {
	background: rgba(0, 0, 0, 0.3);
	border-radius: 8px;
	position: absolute;
	bottom: 10px;
	right: 10px;
	padding: 0 10px;
	a {
		color: lighten($color: $links-color, $amount: 25)
	}
}

.outline-button {
	background-color: $secondary-color;
	border: 0;
	box-sizing: border-box;
	color: #000000;
	display: flex;
	font-size: 1rem;
	font-weight: 700;
	justify-content: center;
	line-height: 1.75rem;
	padding: .75rem 1.65rem;
	position: relative;
	text-align: center;
	text-decoration: none;
	text-decoration-thickness: auto;
	width: 100%;
	max-width: 400px;
	position: relative;
	cursor: pointer;
	transform: rotate(-2deg);
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;

	&::after {
		content: '';
		position: absolute;
		border: 1px solid #000;
		bottom: 4px;
		left: 4px;
		width: calc(100% - 1px);
		height: calc(100% - 1px);
	}

	&:hover::after {
		bottom: 2px;
  		left: 2px;
	}
}

@media #{$desktop} {
	.flex {
		@include flexbox;
		align-items: center;
		flex-direction: row;

		.text {
			-webkit-flex: 1;
			flex: 1;
			padding: 0 20px;
		}
	}

	.content section:nth-child(even) .flex {
		flex-direction: row-reverse;
	}
}
