@import "custom/notepad";

.article-style-default {
	h3 {
		margin: 0;
		a {
			color: #000;
			text-decoration: none;
			font-weight: normal;
			font-size: 1.3em;
		}
	}

	h2 {
		text-align: left;
		font-size: 2.6em;
		margin: 0;
	}

	.article-details {
		border-bottom: 1px solid #eee;
		font-size: .9em;
		margin: 0;
		padding: 40px 0 0 0;

		.share-button {
            float: left;
            padding-right: 10px;
            color: $tertiary-color;

            .fa {
                margin-right: 5px;
            }

			a {
				position: relative;
                text-decoration: none;
				top: -5px;
			}
		}

		a {
			text-decoration: none;
		}

		.article-date {
			float: right;
		}

		&:after {
			content: "";
			display: table;
			clear: both;
		}
	}

	.article-content {
		padding: 40px 0;
        position: relative;
        z-index: 1;

		li  {
			font-size: 1em;
			p {
				font-size: 1em;
			}

            strong {
                font-weight: bold;
            }
		}

        audio {
            margin: 40px auto;
            display: block;
            background-color: $tertiary-color;
            filter: brightness(1.8);
        }

        hr { 
            height: 30px;
            width: 60%;
            border-style: solid; 
            border-color: $tertiary-color; 
            border-width: 1px 0 0 0; 
            border-radius: 20px;
            margin: 40px auto;

            &::before {
                display: block; 
                content: ""; 
                height: 30px; 
                margin-top: -31px; 
                border-style: solid; 
                border-color: $tertiary-color; 
                border-width: 0 0 1px 0; 
                border-radius: 20px; 
            }
        }

        blockquote.callout {
            position: relative;
            margin: 40px auto 80px auto;
            align-self: center;
            border: solid 1px;
            border-radius:20px;
            border-color: $tertiary-color;
            padding: 25px;
            -webkit-transform: rotate(-3deg);
            -moz-transform: rotate(-3deg);
            transform: rotate(-3deg);

            &::before {
                content:"";
                position: absolute;
                width: 80px;
                border: 6px solid #fff;
                bottom: -3px;
                left: 50px;
                z-index: 2;
            }

            &::after {
                content:"";
                position: absolute;
                border: 1px solid $tertiary-color;
                border-radius: 0 50px 0 0;
                width: 60px;
                height: 60px;
                bottom: -61px;
                left: 50px;
                border-bottom: none;
                border-left: none;
                z-index: 3;
            }

            p {
                font-family: $primary-font;
                position: relative;
                font-size: 1.2em;
                color: $primary-color;
                margin: 0;
            }

            cite {
                position: absolute;
                bottom: -61px;
                color: $primary-color;
                font-family: $manuscrit-font;
                font-size: 1.2em;
                margin: 0;
                padding-top: 15px;
                z-index: 1;
                left:105px;
                padding-left:12px;
            }
        }

        blockquote.book {
            display:block;
            background: #fff;
            padding: 15px 20px 15px 45px;
            margin: 0 0 20px;
            position: relative;

            font-family: $secondary-font;
            font-size: 16px;
            line-height: 1.2;
            color: $primary-color;
            text-align: justify;

            border-left: 15px solid $tertiary-color;
            border-right: 2px solid $tertiary-color;

            -moz-box-shadow: 2px 2px 15px #ccc;
            -webkit-box-shadow: 2px 2px 15px #ccc;
            box-shadow: 2px 2px 15px #ccc;

            &::before {
                content: "\201C";
                font-family: $secondary-font;
                font-size: 60px;
                font-weight: bold;
                color: $tertiary-color;
                position: absolute;
                left: 10px;
                top:5px;
            }
            &::after {
                content: "";
            }

            cite {
                margin: 20px 0 0 0;
                display: block;
                font-style: italic;
                color: $tertiary-color;
                text-align: right;
                 &::before {
                    content: "- ";
                 }
            }
        }

        p.border {
            border: 1px solid $tertiary-color;
            color: $tertiary-color;
            border-radius: 10px;
            padding: 10px 20px;
        }
	}

    .doc-download {
        
        position: relative;
        img {
            width: 100%;
            height: auto;
        }
        .button {
            position: absolute;
            text-transform: uppercase;
            top: 10px;
            right: 10px;

            .fa {
                padding-right: 10px;
            }

            a {
                padding: 10px;
                
                @media #{$mid-point} {
                    padding: 15px 30px;
                    
                }
            }
        }

        .button.left {
            top: 10px;
            left: 10px;
        }
        
    }

    .notepad {
        font-family: $manuscrit-font;
        width: 80%;
        list-style-type: none;
        padding-bottom: 40px;
    
        p {
            margin-top: 0;
            line-height: normal;
            font-family: $manuscrit-font;
            font-size: 1em;
            line-height: 1.4;
        }

        li {
            &:before {
                content: "-";
                text-indent: -5px;
            }
    
            margin-bottom: 40px;
        }

        strong {
            font-weight: 600;
        }
    }

    ul.alternated-notes {
        list-style: none;
        li {
            font-family: $manuscrit-font;
            font-size: 2em;
            color: $tertiary-color;
            
            @media #{$desktop} {
                width: -webkit-min-content;
                width: -moz-min-content;
                width: min-content;
                min-width: 300px;
            }
            

            &:nth-child(odd) {
                margin-bottom: 40px;
                left: 30px;
                transform: rotate(-3deg);
    
            }
            
            &:nth-child(even) {
                margin-top: 20px;
                margin-left: 150px;
                right: 30px;
                transform: rotate(3deg);
            }
        }
    }
    
    ul.color-boxes {
        list-style-type: none;
        padding: 0;
        li {
            width: 90%;
            border-radius: 10px;
            padding: 10px 20px;
            margin: 10px 0;
            
            &:nth-child(odd) {
                float: left;
                background-color: $primary-color;
                color: #fff;
            }
            
            &:nth-child(even) {
                float: right;
                background-color: $tertiary-color;
                color: #fff;
            }
            &:last-child {
                margin-bottom: 30px;
            }
        }
    }

    p.em-manuscrit em {
        font-family: $manuscrit-font;
        font-style: normal;
        font-size: 1.3em;
        color: $tertiary-color;
    }

    p.em-underline em, p.strong-underline strong {
        text-decoration: underline;
    }

    p.strong-stroke strong {
        position: relative;
        
        &::before {
            content: " ";
            display: block;
            z-index: -1;
            height: 90%;
            width: 100%;
            margin-left: -3px;
            margin-right: -3px;
            position: absolute;
            background: #ffd500;
            transform: rotate(2deg);
            top: -1px;
            left: -1px;
            border-radius: 20% 25% 20% 24%;
            padding: 10px 3px 3px 10px;
        }
    }

    p.strong-yellow strong {
        border-radius: 1em 0 1em 0;
        background-image: linear-gradient(
            -100deg,
            rgba(255, 224, 0, 0.2),
            rgba(255, 224, 0, 0.7) 95%,
            rgba(255, 224, 0, 0.1)
        );
    }

    p.strong-pen-circle strong {
        position: relative;

        &::before {
            content:"";
            z-index:-1;
            left:-0.5em;
            top:-0.1em;
            border-width:2px;
            border-style:solid;
            border-color:#ef8c22;
            position:absolute;
            border-right-color:transparent;
            width:100%;
            height:1em;
            transform:rotate(2deg);
            opacity:0.7;
            border-radius:50%;
            padding:0.1em 0.25em;
        }

        &::after {
            content:"";
            z-index:-1;
            left:-0.5em;
            top:0.1em;
            padding:0.1em 0.25em;
            border-width:2px;
            border-style:solid;
            border-color:#ef8c22;
            border-left-color:transparent;
            border-top-color:transparent;
            position:absolute;
            width:100%;
            height:1em;
            transform:rotate(-1deg);
            opacity:0.7;
            border-radius:50%;
        }
    }

    p.strong-pen-square strong {
        position: relative;
        padding: 0 5px;

        &::before {
            content:"";
            z-index:-1;
            left:0em;
            top:0em;
            border-width:2px;
            border-style:solid;
            border-color:darkblue;
            position:absolute;
            border-right-color:transparent;
            width:100%;
            height:1em;
            transform:rotate(2deg);
            opacity:0.5;
            border-radius:0.25em;
        }

        &::after {
            content:"";
            z-index:-1;
            left:0em;
            top:0em;
            border-width:2px;
            border-style:solid;
            border-color:darkblue;
            border-left-color:transparent;
            border-top-color:transparent;
            position:absolute;
            width:100%;
            height:1em;
            transform:rotate(-1deg);
            opacity:0.5;
            border-radius:0.25em;
        }
    }
}
