.container, .text-container {
	margin: 0 auto;
	position: relative;
	padding: 0 20px;
}

.text-container {
	max-width: 750px;
}

.container {
	max-width: 1280px;
}

.light-logo {
	align-items: center;
	display: inline-flex;
}

.dark-logo {
	align-items: center;
	display: none;
}

section {
	background: $not-so-white;
}

header {
	color: #fff;
	padding: 15px 0;
	position: fixed;
	width: 100%;
	z-index: 9999;
	line-height: 0;

	.container {
		line-height: 0;
	}

	&.header-background {
		background-color: rgba($not-so-white,.95);
		color: #333;
		box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
		transition: background-color 0.2s ease;
		text-shadow: none;

		a {
			color: #222;
			@media #{$mid-point} {
				&:hover {
					border-bottom-color: rgba($primary-color,0.9);
					border-bottom-width: 5px;
				}
			}
		}

		.dark-logo {
			display: inline-flex;
		}

		.light-logo {
			display: none;
		}
	}

	a {
		color: #fff;
		text-decoration: none;
		z-index: 1;
		position: relative;
		@media #{$mid-point} {
			height: 100%;
		}
		display: block;

		span {
			display: block;
			height: 100%;
		}

		&:hover {
			text-decoration: none;
		}
	}

	.company-name {
		font-size: 1.2em;
		display: inline-block;
		letter-spacing: -1px;

		span {
			padding-left: 5px;

			@media #{$max-desktop} and #{$mid-point} {
				display: none;
			}
		}

		img {
			height: 30px;
			width: auto;
		}
	}
}

.content {
	background: $not-so-white;
}

.info img {
	max-height: 650px;
}

.info {
	padding: 50px 0 20px 0;

	&:first-child {
		padding-top: 100px;
	}
}

.untitled-info {
	&:first-child {
		padding-top: 10px;
	}
}

.collapsed-info {
	padding: 20px 0 20px 0;

	&:first-child {
		padding-top: 50px;
	}
}

.full-width-info {
	width: 100%;
	padding: 0;
	margin: 0;
}

.primary-color-box {
	padding: 10px 10px;
	background-color: $primary-color;
}

.primary-color-box-light {
	padding: 10px 10px;
	background-color: lighten($primary-color, 5);
}

.secondary-color-box {
	padding: 10px 10px;
	background-color: $secondary-color;
}

.tertiary-color-box {
	padding: 10px 10px;
	background-color: $tertiary-color;
}

.square-image {
	position: relative;
	width: 200px;
	min-width: 200px;
	height: 200px;
	border-radius: 200px;
	background-size:cover;
 	background-repeat:no-repeat;
	margin: 0 auto;

	a {
		cursor: pointer;
		text-decoration: none;
		text-transform: uppercase;
		text-align:center;
		font-weight: bold;
		position: absolute;
		bottom: 0;
		left: 0;
		width:100%;
		color: $primary-color;
		background:rgba(255,255,255,0.8);
		padding: 10px 0;
	}
}

.small-text {
	p {
	  font-size: 1.30em;
	}
}
.smaller-text {
	p {
	  font-size: 1.10em;
	}
}

div.v-align-center {
	display: flex;
	align-items: center;
}

div.h-align-center {
	display: flex;
  	justify-content: center;
}

.flex-container {
	display: flex;
	flex-wrap: wrap;
	width: 100%;

	.flex-item { 
		min-width: 0;
		min-height: 0;
		flex-basis: 100%;
		@media #{$desktop} {
			flex-basis: 50%;	
		}
	}

	.flex-item-full {
		min-width: 0;
		min-height: 0;
		flex-basis: 100%;
	}
}
