
.post-title {
    text-align: center;
    h1, h2 {
        text-transform: uppercase;
        font-family: $primary-font;
        font-size: 42px;
        
        em {
            display: block;
            font-family: $secondary-font;
            font-weight: bold;
        }
    }

    a {
        color: inherit;
        text-decoration: none;
    }
}

@media #{$max-desktop} {
    .post-flex {
        article figure {
            display: none;
        }
    }
}

@media #{$desktop} {
    .post-flex {
        .post-title {
            min-width: 50%;
        }

        article figure img {
            object-fit: cover;
            object-position: center;
            width: 400px;
            height: 500px;
            overflow: hidden;
        }

        section:nth-child(even) figure {
            margin-top: -50px;
        }
        section:nth-child(odd) figure {
            margin-bottom: -50px;
        }
    }
}


.post-slide {
    h1.post-slide-title {
        font-family: $manuscrit-font;
        font-weight: 100;
        font-size: 3em;
        margin-left: 20px;
        color: #fff;
        position: relative;

        &::before {
            display: block;
            content: " ";
            width: 300px;
            height: 1px;
            left: 340px;
            background-color: #fff;
            position: absolute;
            top: 60%;
        }
    }
    @media only screen and (max-width: $screen-sm) {
        h1.post-slide-title {
            font-size: 2.7em;
            &::before {
                display: none;    
            }
        }
    }
    

    article {
        position: relative;
        figure {    
            display: block;
            margin-left: .70rem;
            margin-right: .70rem;
    
            img {
                width: 800px;
                object-fit: cover;
                height: 400px;
            }
        }
        h2 {
            color: #fff;
            text-shadow: 1px 1px 1px #000;
            font-size: 24px;
            margin: 0;
        }
        .post-title {
            width: 80%;
            padding: 10px;
            border-radius: 5px;
            background-color: rgba($color: $primary-color, $alpha: 0.8);
            position: absolute;
            bottom: 30px;
            left: 50%;
            -webkit-transform: translate(-50%);
            -moz-transform: translate(-50%);
            transform: translate(-50%);
        }
    }

    article.small-slide {
        figure img {
            height: 200px;
        }

        h2 {
            font-size: 18px;
        }
    }
    
}

.post-row {
    &:after {
        content: "";
        display: table;
        clear: both;
    }

    article {
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-position-x: center;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        min-height: 600px;
        float: left;
        width: 50%;
        padding: 0;
    }
    
    .post-title {
        color: #fff;
        letter-spacing: 5px;
        word-spacing: 10px;
        font-family: $primary-font;
        width: 100%;
        h3 {
            text-transform: uppercase;
            font-size: 34px;
            background-color: rgba($color: #000000, $alpha: 0.2);
            padding: 15px 5px;
        }

        em {
            letter-spacing: 15px;
            font-weight: 200;
            display: block;
            font-family: $secondary-font;
            text-transform: uppercase;
        }
    }
    
    @media screen and (max-width: $screen-sm) {
        article {
            width: 100%;
        }

        .post-title h3 {
            font-size: 28px;
        }
    }
}