#mc_embed_signup {
    form {
        display:block;
        position:relative;
        text-align:left;
    }

    .small-meta {
        font-size: 11px;
    }
    
    .nowrap {
        white-space: nowrap;
    }

    .mc-field-group {
        clear: left;
        position: relative;
        width: 100%;
        min-height: 50px;
        display: grid;
    }

    .indicates-required {
        text-align: right;
        font-size: 11px;
        margin-right:4%;
    }
    
    .asterisk {
        color: #e85c41;
        font-size: 150%;
        font-weight: normal;
        position: relative;
        top:5px;
    }
    
    .clear {
        clear:both;
    }

    div#mce-responses {
        float: left;
        top: -1.4em;
        padding: 0;
        width: 100%;
        margin: 0;
        clear: both;
    }

    div.response {
        float: left;
        top: -1.5em;
        z-index: 1;
        width: 100%;
    }
    
    #mce-error-response {
        color: #e85c41;
        display: none;
    }

    #mce-success-response {
        color: #529214;
        display: none;
    }

    label.error {
        display: block;
        float: none;
        width: auto;
        margin-left: 1.05em;
        text-align: left;
        padding: .5em 0;
    }

    .helper_text {
        color: #8d8985;
        margin-top: 2px;
        display: inline-block;
        padding: 3px;
        background-color: rgba(#FFF, 0.7);
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        font-size: 14px;
        font-weight: normal;
        z-index: 1;
        
        &:empty { 
            border: none;
            background-color: transparent;
        }
    }

    #mc-embedded-subscribe {
        clear: both;
        width: auto;
        display: block;
        margin: 1em 0;
    }

    #mc-embedded-subscribe-form {
        div.mce_inline_error {
            display: inline-block;
            margin: 2px 0 1em 0;
            padding: 3px;
            background-color: rgba(#FFF, 0.7);
            -webkit-border-radius:  3px;
            -moz-border-radius:  3px;
            border-radius:  3px;
            font-size: 16px;
            font-weight: normal;
            z-index: 1;
            color: #e85c41;
        }
        input.mce_inline_error {
            border: 2px solid #e85c41;
        }
    }
}


